@import "../../../utils/styles/common.scss";
@import "../../../utils/styles/skeleton.scss";


.image {
	position: relative;
	
  &__content {
		position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  &__content-skeleton {
		--skeleton-color: #dddbdd;
		@include skeletonFullSizeParent(var(--skeleton-color));
  }
}

